@font-face {
  font-family: Euclid Flex;
  src: url("EuclidFlex-LightItalic.3af12802.woff2") format("woff2"), url("EuclidFlex-LightItalic.93dbdd1a.woff") format("woff");
  font-weight: 300;
  font-style: italic;
  font-display: swap;
}

@font-face {
  font-family: Euclid Flex;
  src: url("EuclidFlex-Semibold.cabf9e9d.woff2") format("woff2"), url("EuclidFlex-Semibold.253aaee2.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Monosten A;
  src: url("Monosten-A.c56d3517.woff2") format("woff2"), url("Monosten-A.8118d3a8.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

* {
  box-sizing: border-box;
  --primary-color: #0059f4;
  --secondary-color: #6a6a6a;
  margin: 0;
  padding: 0;
}

body, html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  user-select: none;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.wrapper {
  background-color: #f6f6f4;
  width: 100vw;
  height: 100vh;
  padding: calc(3.5vw - 10px) 3.5vw 0;
  overflow: hidden;
}

.container {
  width: 100%;
  height: 100%;
  display: flex;
}

.col {
  flex-direction: column;
  width: 50%;
  height: 100%;
  display: flex;
  position: relative;
  overflow: scroll;
}

.col.right {
  width: calc(50% - 3.5vw);
  margin-left: 3.5vw;

  & a {
    text-transform: uppercase;
    margin-top: 20px;
    font-family: Monosten A, monospace;
    font-size: 15px;
    font-weight: 300;

    @media screen and (width <= 991px) {
      display: none;
    }
  }
}

#canvasContainer {
  z-index: 0;
  pointer-events: none;
  width: 100%;
  height: calc(100% - 3.5vw);
  position: absolute;
  overflow: hidden;
}

#canvasContainer canvas {
  display: none;
}

#canvasContainer img {
  visibility: hidden;
  pointer-events: none;
  position: absolute;
}

.bio {
  color: var(--primary-color);
  text-align: justify;
  font-family: Euclid Flex, system-ui, sans-serif;
  font-size: 1.95vw;
  font-weight: 600;
}

@media (width >= 992px) {
  .esp .bio {
    font-size: 1.88vw;
  }
}

.col.left {
  text-transform: uppercase;
  color: var(--primary-color);
  gap: 7px;
  padding-top: 10px;
  font-family: Monosten A, monospace;
  font-weight: 300;
}

h2, h3, ul {
  font-size: 15px;
  line-height: 1;
}

.projects {
  flex-direction: column;
  list-style: none;
  display: flex;
}

.col.left section:last-of-type {
  margin-bottom: 8vw;
}

a {
  color: var(--secondary-color);
  padding-block: 4px;
  text-decoration: none;
  transition: all .25s;
  display: inline-block;
}

a:hover {
  filter: blur(1.5px);
}

.blur-element {
  backdrop-filter: blur(7px);
  pointer-events: none;
  z-index: 999;
  background: linear-gradient(#0000 0%, #f6f6f4 50%);
  width: 50%;
  height: 8vw;
  position: fixed;
  bottom: 0;
  left: 0;
  mask-image: linear-gradient(#0000 0%, #f6f6f4 50%);
}

.arrow {
  z-index: 999;
  width: 1.25vw;
  transition: all .35s;
  position: fixed;
  bottom: 1.5vw;
  left: 3.5vw;
}

.info-wrapper {
  z-index: 9999;
  position: fixed;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.info {
  transform-origin: center;
  white-space: nowrap;
  text-transform: uppercase;
  letter-spacing: .03em;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  font-family: Euclid Flex, system-ui, sans-serif;
  font-size: 12px;
  display: flex;
  transform: rotate(-90deg)translateY(-1100%);

  & h1 {
    color: var(--primary-color);
    text-wrap: auto;
    font-size: 12px;
    line-height: 1;
  }

  & a {
    padding: 0;
  }
}

.esp .info {
  transform: rotate(-90deg)translateY(-1120%);
}

.go-to-website {
  z-index: 1000;
  backdrop-filter: blur(10px);
  text-transform: uppercase;
  letter-spacing: .05em;
  cursor: pointer;
  background: #fffc;
  border: 1px solid #0003;
  border-radius: 9999px;
  padding: .8em 1.6em;
  font-weight: 500;
  transition: all .3s;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (width <= 1336px) {
  h2, h3, ul {
    font-size: 14px;
  }

  h2 {
    margin-top: 5px;
  }

  .bio {
    margin-top: 0;
  }

  .info {
    transform: rotate(-90deg)translateY(-1160%);
  }
}

@media (width <= 1024px) {
  .wrapper {
    padding: 4.5vw 4.5vw 0;
  }

  .info {
    font-size: 10px;
    transform: rotate(-90deg)translateY(-1370%);
  }

  .arrow {
    width: 2vw;
    left: 4vw;
  }

  .bio {
    font-size: 2.5vw;
  }
}

@media (width <= 991px) {
  body, html, .wrapper {
    height: auto;
    overflow: auto;
  }

  .container {
    flex-direction: column-reverse;
    gap: 4vh;
  }

  .col, .col.right {
    width: 100%;
    height: auto;
    margin: 0;
    overflow: visible;
  }

  .bio {
    font-size: 3.8vw;
    line-height: 1.175;
  }

  .info-wrapper {
    margin-block: 4vh;
    position: relative;
    top: 0;
    transform: none;

    & .info {
      flex-wrap: wrap;
      justify-content: flex-start;
      row-gap: 10px;
      font-size: 15px;
      transform: none;

      & h1 {
        font-size: 15px;
      }
    }
  }

  h2, h3, ul {
    font-size: 15px;
    line-height: 1.3;
  }

  .blur-element, .arrow {
    display: none;
  }

  #canvasContainer {
    pointer-events: none;
    width: 95vw;
    height: 80vh;
    position: fixed;
    top: 10vh;
    left: 2.5vw;
  }
}

@media (width <= 767px) {
  body, html, .wrapper {
    height: auto;
    overflow: auto;
  }

  .container {
    flex-direction: column-reverse;
    gap: 4vh;
  }

  .col, .col.right {
    width: 100%;
    height: auto;
    margin: 0;
    overflow: visible;
  }

  .bio {
    font-size: 5.5vw;
  }

  .info-wrapper {
    & .info {
      row-gap: 10px;
      font-size: 13px;

      & h1 {
        font-size: 13px;
      }
    }
  }

  h2, h3, ul {
    font-size: 13px;
    line-height: 1.3;
  }
}
